
import Vue from "vue";
import store from "@/store";
import API from "@/api/API";

export default Vue.extend({
  name: "Create",

  components: {
    ata: () => import("../orderComponents/AtaPreview.vue"),
    aaij: () => import("../orderComponents/AAIJPreview.vue"),
    exp: () => import("../orderComponents/EXPPreview.vue"),
    eval: () => import("../orderComponents/EVALPreview.vue"),
    cert: () => import("../orderComponents/CERTPreview.vue")
  },

  data: () => ({
    model: {
      documents: [] as Array<any>,
      goods: [] as Array<any>,
      details: { countries: [] as Array<any> } as any,
      order: {
        exchange_rate: "1.0000",
        document_type: "original",
        source: "operator",
        release_mode: "normal",
        handler_id: 1,
        valid_form: new Date().toISOString().substr(0, 10)
      } as any
    } as any,
    errorMessages: {} as any,
    loading: false as boolean,
    breadcrumb: [
      {
        text: "orders.title",
        disabled: false,
        href: "/orders"
      },
      {
        text: "orders.form.header.preview",
        disabled: true,
        href: `/orders/preview`
      }
    ],
    selectedTab: 0 as number,
    component: "" as string,
    orderComponents: {
      1: "ata",
      2: "aaij",
      3: "cert",
      4: "eval",
      5: "exp"
    } as any,
    comments: {} as any,
    oldComments: null as any,
    isLoading: false as boolean
  }),

  async mounted() {
    await this.loadData();
  },

  methods: {
    async submit(): Promise<void> {
      this.isLoading = true;

      try {
        const comments = [] as Array<any>;

        for (const key in this.comments) {
          if (this.comments[key]?.text) {
            comments.push(this.comments[key]);
          }
        }

        await this.$API
          .orders()
          .updateComment(Number(this.$route.params.id), { notes: comments });
        this.oldComments = JSON.stringify(this.comments);
        await this.$router.push("/orders");
        await this.$store.dispatch(
          "alert/showSuccess",
          this.$t("global-alert.successful_edit")
        );
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;
          setTimeout(() => {
            this.errorMessages = {};
          }, 2000);
        }
        await this.$store.dispatch("alert/showError", e.message || e);
      }
      this.isLoading = false;
    },
    hasChanges(): boolean {
      return JSON.stringify(this.comments) !== this.oldComments;
    },
    async loadData() {
      this.loading = true;
      try {
        const item = await API.orders().show(Number(this.$route.params.id));
        this.component = this.orderComponents[item.order.service_type_id];

        const items = await this.$API
          .orders()
          .getComments(Number(this.$route.params.id));
        this.comments = this.parseComments(items);
        this.oldComments = JSON.stringify(this.comments);

        this.$nextTick(() => {
          this.fullLoaded = true;
          this.model = item;
        });
      } catch (e) {
        await store.dispatch("alert/showError", e.message);
      }
      this.loading = false;
    },
    parseComments(array: any) {
      const object = {} as any;

      for (const key of array) {
        object[key.key] = key;
      }
      return object;
    },
    setComments(comments: any) {
      this.comments = comments;
      this.$forceUpdate();
    },
    getCount(): number {
      return Object.keys(this.comments).length;
    }
  }
});
